import React, { useState } from "react";
import "./Cars.css";
import { requestGetAllCars } from "../../API/cars/gerCars";
import { requestGetFilteredCars } from "../../API/cars/getFilteredCars";
import { useEffect } from "react";
import Card from "../Card/Card";
import { selectFilter } from "../../redux/cars/carSelectors";
import { useSelector } from "react-redux";
import { PER_PAGE } from "../../services/globalVariables";

const Cars = () => {
  const [cars, setCars] = useState([]);
  const filter = useSelector(selectFilter);
  const pages = cars.length <= PER_PAGE ? 1 : Math.ceil(cars.length / PER_PAGE);
  let numberPage = 1;

  useEffect(() => {
    if (!filter) {
      requestGetAllCars().then((value) => {
        setCars([...value]);
      });
    } else {
      requestGetFilteredCars(filter).then((value) => {
        setCars([...value]);
      });
    }
  }, [filter]);

  return (
    <div className="car-box">
      <ul className="car-catalog">
        {cars &&
          cars.map((car) => (
            <li key={car.id}>
              <Card car={car}></Card>
            </li>
          ))}
      </ul>
      <div className="pagination">      
          <div key='page' className="item-pagination">1</div>;       
      </div>
    </div>
  );
};

export default Cars;
